$orange: #FFA500;
@mixin font-main() {
    font-family: 'Roboto', sans-serif;
    color: $orange;
}
@mixin arrow() {
    font-size: 60px;
    color: black;
    background: lightgray;
    opacity: 0.25;
    position: fixed;
    top: 250px;
}

.nav-slide {
    background-color: $orange;
    padding-top: 3px;
    padding-bottom: 3px;
    
    .slide-link {
        text-align: center;
    }
    a{
        color: white;
        font-weight: bold;
    }
}

h1 {
    text-align: center;
    margin-top: 5px;
    margin-bottom: 5px;
}

.slide-screenshot {
    display: block;
    width: 85%;
    height: auto;
    margin-right: auto;
    margin-left: auto;
}

.app-links {
    background-color: white;
    margin-top: 10px;
    margin-bottom: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
    text-decoration: underline;
}

.app-link {
    margin: 10px;
    font-size: 16px;
    @include font-main();
}

.link-divider {
    color: $orange;
    font-size: 16px;
}

.slide-description {
    background-color: white;
    padding: 10px;
    margin-bottom: 10px;
}

.arrow-back {
    @include arrow();
}
.arrow-forward {
    @include arrow();
    right: 0px;
}
.arrow-back:hover {
    opacity: 0.6;
}
.arrow-forward:hover {
    opacity: 0.6;
}

// small screens
@media only screen and (max-width: 768px) {
    .arrow-back {
        font-size: 45px;
    }
    .arrow-forward {
        font-size: 45px;
    }
}